Vue.use(Vuetify)

const vuetify = new Vuetify({
  theme: {
    themes: {
      light: {
        primary: '#607d8b',
        secondary: '#848482',
        success: '#75816b',
        error: '#993333',
        info: '#6699cc',
      },
    },
  },
})

export default vuetify